$appfont : 'outfit';
$appColor : #F3B539;
body{
  background-image :url('../src/assets/image/pokeball-icon.png');
  background-repeat: no-repeat;
}
.App {
  text-align: center;
  font-family: appfont;
}
@font-face {
  font-family: "outfit";
  src: url(../assets/fonts/Outfit-VariableFont_wght.ttf);
}
.main-loader{
  text-align: center;
    margin-top: 30% !important;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.container-body{
  display: flex;
  justify-content: space-evenly;
  align-content: center;
  width: 100%;
  height: 100%;
  margin-top: 5%;
  row-gap: 5;
  background-image :url('../src/assets/image/pokeball-icon.png');
  background-repeat: no-repeat;
  background-size: cover;
}
.container{
  border-radius: 20px;
  height: 30px;
  box-shadow: #ededdb 0 -1px 10px;
  padding:15px;
  background-color: white;
}
.show-case-container{
  width: 70%;
  margin-right: 200px;
}
.card-handler {
  display: flex;
  flex-wrap: wrap;
  flex-grow:3;
  height: fit-content;
  padding:20px;
  flex-direction: column;
  flex: 0.5;
  min-width: 20%;
  margin: 10px;
  margin-top: 60px;
  padding-top: 40px;
  position: relative;
  cursor: pointer;
  border: 2px solid white;
  max-height: 150px;
  max-width: 150px;
}
.position-relative{
  position: relative;
}
.detail-container{
  width: 320px;
  margin: 10px;
  height: 82vh;
  position: fixed;
  right: 20px;
  padding: 0 15px;
  text-align: center;
  top:175px;
  bottom: 0;
  margin-bottom: 0;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.detail-content{
  margin-top:100px
}
.text-center{
  text-align: center;
}
.text-desc{
  color:#8F9396;
  font-weight:200;
}
.column-grids{
  display: flex;
  padding: 12px;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.tabs{
  width: 50%;
}
.tabs div:nth-child(even){
    background-color: #F6F8FC;
    padding: 8px 0;
    border-radius: 30px;
    margin: 5px;
    
}
.card-handler:hover {
  border: 2px solid #e0e0e0;
  // transform: scale(30px);
}
.card-handler:hover .imager img{
  // border: 2px solid #e0e0e0;
  transform: scale(1.1);
}
.animatedImage{
  position:absolute;
  left: 32%;
  bottom: 94%;
}
.animatedImage img{
  width: 150px;
  height: 150px;
}
.imager{
  position:absolute;
  left: 30%;
  bottom: 70%;
}
.imager img{
  width: 100px;
  height:100px;
  transition: transform 0.2s;
  transition-duration: 100ms;
}
.cards-container{
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  row-gap: 30px;

}
.search-barContainer{
  margin: 30px 10px;
  position: relative;
}
.text-header{
  text-align: center;
}
.search-btn{
  position:absolute;
  background-color:  #F3B539;
  border: 0px none;
  border-radius: 10px;
  padding: 10px;
  top: 10px;
  right: 17px;
  cursor: pointer;
  box-shadow: #F3B539 5px 5px 15px;
}
.highlighted-card{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top:10px
}
.card-css{
  font-size: 16px;
  padding: 12px;
  font-weight: 800;
  opacity: 0.8;
  border-radius:10px;
}

.search-barContainer input{
  border: 0px none;
  padding: 7px;
  width: 90%;
}
.abilities{
  display: flex;
  justify-content: space-around;
}
.mt-20{
  margin-top: 20px;
}
.lh-500{
  line-height: 500px;
}
.search-barContainer input:focus-visible{
  outline: 0px none;
}
.cyllinder-cards{
  bottom:100px;
  display: flex;
  justify-content: space-around;
  
}
.scroll-loader{
  text-align: center;
  margin-top: 10px !important;

}
.position-absolute{
  position:absolute;
}
.cyllinder-outer{
  background: #F6F8FC;
  display: flex;
  padding: 5px;
  border-radius: 30px;
  margin: 5px;
  flex-direction: column;
}
.ranks{
  margin: 6px 0
}
.active{
  top:170px !important
}
.loader-image{
  margin: auto;
  width: 100%;
  line-height: 0 !important;
  transition: all 0.3s ease;
}
.loader-image .rotate-loader{
  animation: spin 1.5s linear infinite;
}
.cyllinder-circle{
  background-color:#DF2140;
  border-radius: 50%;
    height: 25px;
    width: 25px;
    color: white;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 10px;
}
@keyframes spin {
  0%{
    transform: rotate(350deg)
  };
}

@media only screen and (max-width: 360px) {
  .detail-container{
    right: 5px;
    height: 100%;
  }
  .active{
    top:150px !important
  }
  .cards-container{
    justify-content: flex-start;
  }
}

@media only screen and (min-width:370px) and (max-width: 375px) {
  .detail-container{
    right: 13px;
    height: 100%;
  }
  .active{
    top:20px !important
  }
  .cards-container{
    justify-content: flex-start;
  }
}

@media only screen and  (min-width:390px) and (max-width: 400px) {
  .detail-container{
    right: 19px;
  }
  .cards-container{
    justify-content: flex-start;
  }
}

@media only screen and  (min-width:410px) and (max-width: 500px) {
  .detail-container{
    right: 32px;
  }
  .cards-container{
    justify-content: flex-start;
  }
}

@media only screen and (max-width: 600px) {
  .card-handler{
    min-width: 100%;
    
  }
  .cards-container{
    justify-content: flex-start;
  }
  .show-case-container{
    margin-right:53px;
  }
  .active{
    top:170px;
  }
  
  .search-barContainer{
    width: 100%;
  }
  .detail-container{
    margin: auto;
    top:10000px;
    transition: all .3s ease-out;
  }
}
@media only screen and (min-width:1618){
  .cards-container{
    column-gap:90px;
  }
}
@media only screen and  (min-width:700px) and (max-width: 1000px) {
  .detail-container{
        /* margin: auto; */
    width: 95%;
    top: 10000px;
    right: -6px;
  }
  .animatedImage{
    left: 50%;
  }
  .active {
    top: 126px !important;
  }
  .lh-500{
    line-height: 200px;
  }
  .show-case-container{
    margin-right:0px;
  }
  .card-handler{
    min-width: 38%;
  }
}